@import url(https://fonts.googleapis.com/css?family=Oswald|PT+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.SearchBar-wrapper{
  position: relative;
}

.search-movie-input{
  width: 100%;
  border-bottom: 1px solid #ccc;
  border-top: none;
  border-left: none;
  border-right: none;
  padding: 18px 38px;
  font-size: 14px;
  background-color: #282c34;
  color: white;
  -webkit-appearance: none;
}

.search-movie-input::-webkit-input-placeholder{
  color: rgba(255, 255, 255, 0.5);
}

.search-movie-input::-ms-input-placeholder{
  color: rgba(255, 255, 255, 0.5);
}

.search-movie-input::placeholder{
  color: rgba(255, 255, 255, 0.5);
}

.search-movie-input:focus{
  outline: 0;
}

.search-movie-input-icon{
  position: absolute;
  top: 50%;
  left: 25px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 15px;
  height: auto;
}

.search-movie-input-icon path{
  fill: #FFF;
}

.App-header {
  color: #ccc;
  text-align: left;
}

.App-header .App-title {
  font-size: 20px;
  font-weight: bold;

}
.sidebar-menu{
  list-style-type: none;
  margin: 0;
  padding: 25px 0;
  font-size: 14px;
}

.sidebar-menu__item {
  float: left;
  margin-right: 1%;
  background: #bbb;
  border-radius: 5px;
}

.sidebar-menu__item a{
  text-decoration: none;
  padding: 10px 25px;
  display: block;
  transition: all .3s ease;
  width: 100px;
  text-align: center;
  font-weight: bold;
  color: #ffffff;
}

.sidebar-menu__item a.is-active,
.sidebar-menu__item:hover a {
  background: #31dfb7;
  color: #000000 !important;
}
@-webkit-keyframes modal-video {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes modal-video {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes modal-video-inner {
  from {
    -webkit-transform: translate(0, 100px);
            transform: translate(0, 100px);
  }

  to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

@keyframes modal-video-inner {
  from {
    -webkit-transform: translate(0, 100px);
            transform: translate(0, 100px);
  }

  to {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

.modal-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  z-index: 1000000;
  cursor: pointer;
  opacity: 1;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-name: modal-video;
          animation-name: modal-video;
  transition: opacity .3s ease-out;
}

.modal-video-effect-exit {
  opacity: 0;
}

.modal-video-movie-wrap {
  -webkit-transform: translate(0, 100px);
  transform: translate(0, 100px);
}


.modal-video-body {
  max-width: 940px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: table;
}

.modal-video-inner {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  height: 100%;
}

.modal-video-movie-wrap {
  width: 100%;
  height: 0;
  position: relative;
  padding-bottom: 56.25%;
  background-color: #333;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-name: modal-video-inner;
          animation-name: modal-video-inner;
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  transition: -webkit-transform .3s ease-out;
  transition: transform .3s ease-out;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;  
}

.modal-video-movie-wrap iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.modal-video-close-btn {
  position: absolute;
  z-index: 2;
  top: -35px;
  right: -35px;
  display: inline-block;
  width: 35px;
  height: 35px;
  overflow: hidden;
  border: none;
  background: transparent;

}

.modal-video-close-btn:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.modal-video-close-btn:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.modal-video-close-btn:before,
.modal-video-close-btn:after {
  content: '';
  position: absolute;
  height: 2px;
  width: 100%;
  top: 50%;
  left: 0;
  margin-top: -1px;
  background: #fff;
  border-radius: 5px;
  margin-top: -6px;
}

.list-container__movie-item{
  -webkit-flex-basis: 0;
          flex-basis: 0;
  margin-right: 4%;
  margin-bottom: 30px;
  position: relative;
  transition: opacity 0.3s ease;
  text-align: left;
}

.list__movie-image{
