}

.list__movie-actions .icons {
  float: right;
  margin-top: 4px;
}

.list__movie-actions .icons svg {
  padding: 0 5px;
}
.list__movie-action{
  fill: #FFF;
  transition: fill 0.4s ease;
  cursor: pointer;
  height: 15px;
