} 

.detail-list .flex {
  display: table;
}

.list-title{
  width: 120px; 
  color: #ffffff;
  margin-bottom: 10px;
}

.line {
  background: #50E3C2;
  height: 2px;
  display: table;
  width: 100%;
}

.overview {
  display: table;
  width: 100%;
}

.movie-overview {
  color: #bbb;
